import React, {Fragment} from 'react';
import { PROJECTS } from './Content'
import Typography from '@material-ui/core/Typography';

const Description = ({paragraphs}) => {
  const text = paragraphs.map(paragraph => {
    return (<p>{paragraph}</p>)
  })
  return text
}

const FeaturesList = ({items}) => {
  const mappedFeatures = items.map(feature => {
    return (<li>{feature}</li>)
  })
  return (
    <Fragment>
      <Typography component="h3" variant="h5">
        Features
      </Typography>
      <ul>
        {mappedFeatures}
      </ul>
    </Fragment>

  )
}

const Project = () => {
  const projects = PROJECTS.map(project => {
    return (
      <div>
        <Typography
            component="h2"
            variant="h5"
            color="default"
            align="center"
            noWrap
          >
            {project.title}
          </Typography>
          <Description paragraphs={project.description} />
          <FeaturesList items={project.features} />
      </div>
    )
  })
  return(
    <Fragment>
      {projects}
    </Fragment>
  )
}
export default Project
