import React from 'react';
import { CONTACT_INFO } from './Content'

const Contact = () => {
  const contactTypes = Object.keys(CONTACT_INFO)
  const contactInformationList = contactTypes.map(type => {
    const refTag = type === 'email'? `mailto:${CONTACT_INFO[type]}`: CONTACT_INFO[type]
    return (
      <li key={type} >
        <strong>{`${type}: `}</strong>
        <a href={refTag}>{CONTACT_INFO[type]}</a>
      </li>
    )
  })

  return(
    <div>
      Contact
      <ul>
        {contactInformationList}
      </ul>

    </div>
  )
}
export default Contact
